@import url(https://fonts.googleapis.com/css2?family=Pacifico&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Great+Vibes&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@400;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Titillium+Web&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
html,
body {
  margin: 0;
  padding: 0;
}
* {
  margin: 0;
  padding: 0;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


html,
body {
  height: 100%;
}
.home {
  width: 100%;
  height: 100%;
}
.home__mobfb {
  display: none;
}
.home__body {
  display: flex;
  height: 100%;
  width: 100%;
}
.home__image {
  position: absolute;
  top: 0;
  width: 100%;
  max-height: 90%;
  z-index: -1;
  object-fit: cover;
  overflow: auto;
  -webkit-mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
          mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
}
.home__container {
  display: flex;
  height: 100%;
  width: 100%;
  flex-direction: column;
  position: relative;
  top: 5%;
}

.home__landing {
  display: flex;
  width: 100%;
  height: 100%;
  margin-top: 5%;
  margin-bottom: 5%;
  flex-wrap: wrap;
  justify-content: space-evenly;
}
.home__branding {
  flex: 0.7 1;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  width: 80%;
  padding: 20px;
  margin-top: 10px;
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
  background-image: radial-gradient(
    ellipse,
    rgba(255, 0, 0, 0.5),
    rgba(0, 0, 0, 0)
  );
  border-radius: 10px;
  max-height: 700px;
  max-width: 900px;
}
.home__branding > img {
  object-fit: contain;
  padding-bottom: 20px;
}
.home__branding > h1 {
  color: rgb(255, 255, 255);
  font-family: "Lato", sans-serif;
  font-size: 300%;
  text-align: center;
}

.home__branding > h3 {
  color: rgb(255, 255, 255);
  text-align: center;
  font-family: "Montserrat", sans-serif;
  font-size: 2vw;
  font-weight: 100;
}
.home__facebook {
  flex: 0.3 1;
  border-radius: 20px;
  background-color: white;
  padding: 10px;
  margin-top: 0px;
  min-height: 50vh;
  height: 100%;
  width: 100%;
}

.home__testimonials {
  margin-top: 5%;
  background-image: linear-gradient(to bottom, white, orange);
}

.home__testimonialsTitle {
  background-image: radial-gradient(
    circle,
    rgb(255, 222, 39),
    rgba(1, 1, 1, 0)
  );
}

.home__testimonialsTitle > h1 {
  color: white;
  text-align: center;
  font-family: "Great Vibes", cursive;
  font-size: 6vw;
  font-weight: 100;
}
@media screen and (max-width: 420px) {
  .home__image {
    display: none;
  }
  .home__mobfb {
    display: flex;
    background-color: #fff;
    margin: auto;
    width: 90%;
    margin-top: 1vh;
    border-radius: 10px;
  }
  .home__mobfbImage {
    height: 10vh;
    border-radius: 10px;
    overflow: hidden;
    margin: auto;
  }
  .home__body {
    background-image: radial-gradient(circle, purple, #ff8922);
  }
  .home__branding {
    -webkit-backdrop-filter: blur(5px);
            backdrop-filter: blur(5px);
    margin: auto;
    background-color: rgba(255, 255, 255, 0.5);
  }
  .home__branding > h1 {
    font-size: 10vw;
    margin-bottom: 5%;
  }
  .home__branding > h3 {
    font-size: 4vw;
  }
  .home__landing {
    display: flex;
    flex-direction: column;
  }
  .home__facebook {
    display: none;
  }
  .home__testimonials {
    background-image: none;
  }
}

.header {
  background-image: linear-gradient(to bottom, rgb(200, 0, 0), red);
  min-height: 10vh;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  z-index: 100;
  box-shadow: 0px 1px 5px #27272780;
  -webkit-box-shadow: 0px 1px 5px #27272780;
  -moz-box-shadow: 0px 1px 5px #27272780;
}
.header__left {
  display: flex;
  justify-content: space-evenly;
}
.header__logo {
  object-fit: contain;
  max-height: 10vh;
}
.header__title {
  color: white;
  display: flex;
  justify-content: center;
}
.header__title > h1 {
  font-family: "Roboto Slab", serif;
  color: white;
  font-weight: 100;
  margin: auto;
  margin-left: 2vw;
}

.header__right {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin: auto;
  margin-right: 0;
}
.header__nav {
  display: flex;
  justify-content: space-between;
}
.header__navitem > h3 {
  font-family: "Lato", sans-serif;
  color: white;
  padding: 1vw;
}
.header__icons{
  margin: auto;
}
.header__icon {
  position: relative;
  right: 0;
  color: white !important;
  padding-left: 7px;
  padding-right: 7px;
}
.header__googleLogo {
  width: 20px;
}

@media screen and (max-width: 420px) {
  .header__right {
    display: none;
  }
}

.cards {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  min-height: 350px;
  /* margin: 20px; */
}
.card__action {
  position: relative;
  bottom: 0px;
  max-width: 150px;
  border-radius: 50px !important;
  padding-left: 20px !important;
  padding-right: 20px !important;
  background-color: #fffefe !important;
  font-weight: bold !important;
}

.card {
  display: flex;
  flex-direction: column;
  padding: 20px;
  flex: 0.3 1;
  flex-grow: 1;
  min-width: 300px;
  margin: 10px;
  border-radius: 10px;
  background-color: rgb(74, 100, 148);
  transition: all 0.2s ease-in-out;
  padding-bottom: 40px;
  justify-content: space-evenly;
  color: white;
}
.card:hover {
  transform: scale(1.1);
  background-color: white;
  color: red;
  font-size: larger;
}
.card__headline {
  margin-bottom: 10px;
}

.card__description {
  margin-bottom: 30px;
}

.card__title {
  margin-bottom: 10px;
}

.card__headline > h1 > .MuiSvgIcon-root {
  font-size: 40px !important;
}

@media screen and (max-width: 420px) {
  .card {
    padding: 10px;
  }
  .card:hover {
    background-color: white;
    color: red;
  }
}

.testimonials {
  margin: 1vw;
  padding: 0;
  height: 100%;
  width: 100%;
  font-family: sans-serif;
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
}
@media screen and (max-width: 420px) {
  .testimonials {
    margin: 0;
  }
}

.testimonialCard {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 40px;
  position: relative;
  width: 40vw;
  padding: 2vw;
  box-shadow: 0 10px 10px rgba(0, 0, 0, 0.5);
  overflow: hidden;
  border-radius: 10px;
  background-color: #f9f8f7;
}

.testimonialCard__body {
  display: flex;
  margin-bottom: 5%;
}
.testimonialCard__body > p {
  font-family: "Open Sans", sans-serif;
  margin: auto;
  margin-left: 1vw;
}
.testimonialCard__avatar {
  width: 5vw !important;
  height: 5vw !important;
  float: left;
}
.testimonialCard__details {
  text-align: center;
}

@media screen and (max-width: 420px) {
  .testimonialCard {
    margin: 10px;
    background-color: rgba(0, 0, 0, 0);
    box-shadow: none;
    width: 100%;
    display: flex;
    flex-direction: column;
    min-height: 100px;
    min-width: 200px;
    padding-top: 50px;
  }
  .testimonialCard__body {
    flex-direction: column;
  }
  .testimonialCard__content {
    display: flex;
    flex-direction: column;
    border: #ffde27 solid 1px;
    border-radius: 10px;
    padding: 20px;
    background-color: #f9f8f7;
  }
  .testimonialCard__avatar {
    width: 20vw !important;
    height: 20vw !important;
    margin: auto;
    margin-top: -20vw;
  }

  .testimonialCard__body > p {
    margin-top: 5%;
  }
  /* ###################################################### */
  .testemonial {
    margin: 20px;
  }

  .testemonial__container {
    margin: 20px;
    justify-content: space-between;
  }

  .testemonial__title {
    display: flex;
    justify-content: space-between;
  }

  .testemonial__title > h1 {
    margin: auto;
    margin-left: 0;
    padding-bottom: 10px;
    font-size: 4vw;
  }

  .testemonial__title > h3 {
    margin: auto;
    margin-right: 0;
    font-size: 2vw;
  }

  .testemonial__body {
    display: flex;
    overflow: hidden;
  }
  .testemonial__body > p {
    padding: 20px;
    padding-left: 0px;
    word-wrap: break-word;
    max-height: 200px;
    font-size: 2vw;
  }

  .testemonial__treat {
    padding: 10px;
    padding-left: 0;
    margin-bottom: 50px;
    position: relative;
    bottom: 0;
    font-size: 2vw;
  }

  .testemonial__avatar {
    border: #ffde27 solid 10px;
    width: 10vw !important;
    height: 10vw !important;
  }

  .testemonial__avatarDiv {
    display: flex;
    justify-content: center;
    margin-bottom: -8vw;
  }
  /* ######################################################################### */
}

